import React, { Component } from 'react';
import PropTypes from 'prop-types';

export class Todoitem extends Component {
    getStyle = () => {
        return {
            background: '#f4f4f4',
            padding: '10px',
            borderBottom: '1px #ccc dotted',
            textDecoration: this.props.todo.completed? 'line-through':'none'
        }
    }

    render() {
        const { id, title } = this.props.todo
        return (
            <div style={this.getStyle()}>
                <p>
                    <input type = "checkbox" onChange = { this.props.markComplete.bind(this, id) }/> {'  '}
                    {title}
                    {/* <button onClick={this.props.deleteTodo.bind(this,id)} style={btnStyle}>x</button> */}
                    {/* <img src="https://img.icons8.com/dusk/64/000000/delete-sign.png" onClick={this.props.deleteTodo.bind(this,id)} style={imgStyle}/> */}
                    <img src="data:image/png;base64," onClick={this.props.deleteTodo.bind(this,id)} style={imgStyle}/>
                 </p>  
            </div>
        )
    }
}

Todoitem.propTypes = {
    todo: PropTypes.object.isRequired,
    markComplete: PropTypes.func.isRequired,
    deleteTodo: PropTypes.func.isRequired
}

const btnStyle = {
    background: '#ff0000',
    color: '#fff',
    border: 'none',
    padding: '5px 9px',
    borderRadius: '50%',
    cursor: 'pointer',
    float: 'right'
}

const imgStyle = {
    padding: '5px 9px',
    cursor: 'pointer',
    float: 'right',
    width: '40px',
    border: '1px'
}


export default Todoitem
